import {
  CCard,
  CCardTitle,
  CDataTable,
  CButton,
  CModal,
  CModalHeader,
  CModalBody,
  CModalFooter,
  CInput,
  CAlert,
} from "@coreui/react";
import React, { useEffect, useState } from "react";
import DeleteForeverIcon from "@material-ui/icons/DeleteForever";
import NoteAddIcon from "@material-ui/icons/NoteAdd";
import axios from "axios";
import { useSelector } from "react-redux";
import { selectUser } from "../../localyStore";
const Coupon = () => {
  const [loading, setLoading] = useState(true);
  const user = useSelector(selectUser);
  const [coupon, setCoupon] = useState("");
  const [reload, setReload] = useState(false);
  const [addModal, setAddModal] = useState(false);
  const [newCoupon, setNewCoupon] = useState();
  const server = process.env.REACT_APP_SERVER;

  // alert
  const [event, setEvent] = useState(null);

  const clearEvent = (ms) => {
    setTimeout(() => {
      setEvent(null);
    }, ms);
  };

  const Toggle = () => {
    setAddModal(!addModal);
    setNewCoupon("");
  };
  //update coupon
  const updateCoupon = () => {
    axios
      .put(
        server + process.env.REACT_APP_ADMIN_COUPON_UPDATE + newCoupon._id,
        { ...newCoupon },
        {
          headers: {
            Authorization: `Bearer ${user?.token}`,
          },
        }
      )
      .then((res) => {
        setReload(!reload);
        Toggle();
        setNewCoupon({
          couponLimit: "",
          discountStatus: "",
          createdAt: "",
          updatedAt: "",
          _id: "",
          couponCodeName: "",
          discount: "",
          validPriceRange: "",
          expirationTime: "",
          __v: "",
        });
        setEvent({
          type: "success",
          massage: res.data.message,
        });
        clearEvent(5000);
      })
      .catch((err) => {
        setEvent({
          type: "error",
          massage: err.response.data.message,
        });
        clearEvent(5000);
      });
  };
  //delete later api not provided
  const deleteCoupon = (id) => {
    axios
      .delete(server + process.env.REACT_APP_ADMIN_COUPON_DELETE + id, {
        headers: {
          Authorization: `Bearer ${user?.token}`,
        },
      })
      .then((res) => {
        setReload(!reload);
        setNewCoupon("");
        setEvent({
          type: "success",
          massage: res.data.message,
        });
        clearEvent(5000);
      })
      .catch((err) => {
        setEvent({
          type: "error",
          massage: err.response.data.message,
        });
        clearEvent(5000);
      });
  };
  //add new address for shiping
  const addCoupon = () => {
    axios
      .post(
        server + process.env.REACT_APP_ADMIN_COUPON_POST,
        { ...newCoupon },
        {
          headers: {
            Authorization: `Bearer ${user?.token}`,
          },
        }
      )
      .then((res) => {
        setReload(!reload);
        setAddModal(false);
        setNewCoupon({
          couponLimit: "",
          discountStatus: "",
          couponCodeName: "",
          discount: "",
          validPriceRange: "",
          expirationTime: "'",
        });
        setEvent({
          type: "success",
          massage: res.data.message,
        });
        clearEvent(5000);
      })
      .catch((err) => {
        setEvent({
          type: "error",
          massage: err.response.data.message,
        });
        clearEvent(5000);
      });
  };
  useEffect(() => {
    axios
      .get(server + process.env.REACT_APP_ADMIN_COUPON_GET, {
        headers: {
          Authorization: `Bearer ${user?.token}`,
        },
      })
      .then((res) => {
        setCoupon(res.data.coupon);
        setLoading(false);
      })
      .catch((err) => {
        setLoading(false);
        setEvent({
          type: "error",
          massage: err?.response?.data.message ?? "erorr on server",
        });
        clearEvent(5000);
      });
  }, [reload, user]);
  return (
    <div>
      <CCard className="p-4">
        {/* {for coupon only } */}
        <CCardTitle>Avaliable Coupons : </CCardTitle>
        <hr />
        <div>
          <CButton
            style={{ float: "left" }}
            color="danger"
            onClick={() => setAddModal(!addModal)}
            disabled
          >
            Delete All Coupon
          </CButton>
          <CButton
            style={{ float: "right" }}
            variant="outline"
            color="success"
            onClick={() => setAddModal(!addModal)}
          >
            Add Coupon
          </CButton>
        </div>
        <hr />
        <CDataTable
          tableFilter
          loading={loading}
          items={coupon}
          fields={[
            { key: "SiNo.", _style: { width: "4%" } },
            {
              key: "couponCodeName",
              _style: { width: "12%" },
            },
            { key: "discount", _style: { width: "10%" } },
            { key: "couponLimit", _style: { width: "10%" } },
            { key: "discountStatus", _style: { width: "10%" } },
            { key: "validPriceRange", _style: { width: "10%" } },
            { key: "expirationTime", _style: { width: "10%" } },
            { key: "Edit", _style: { width: "5%" } },
            { key: "Delete", _style: { width: "5%" } },
          ]}
          hover
          sorter
          outlined
          scopedSlots={{
            "SiNo.": (item, index) => <td key={index}>{index + 1}</td>,
            expirationTime: (item, index) => (
              <td key={index}>
                {new Date(parseInt(item.expirationTime)).toLocaleDateString()}-
                {new Date(parseInt(item.expirationTime)).toLocaleTimeString()}
              </td>
            ),
            Edit: (item, index) => (
              <td key={index}>
                <CButton
                  className="d-flex"
                  variant="outline"
                  color="success"
                  onClick={() => {
                    setAddModal(true);
                    setNewCoupon(item);
                  }}
                >
                  <NoteAddIcon /> Edit
                </CButton>
              </td>
            ),
            Delete: (item, index) => (
              <td key={index}>
                <CButton
                  className="d-flex"
                  variant="outline"
                  color="danger"
                  onClick={() => deleteCoupon(item._id)}
                >
                  <DeleteForeverIcon /> Delete
                </CButton>
              </td>
            ),
          }}
        />
        <hr />
        <CModal
          className="sm-30"
          show={addModal}
          onClose={() => setAddModal(false)}
        >
          <CModalHeader closeButton>Coupon Form</CModalHeader>
          <CModalBody>
            <label>CouponCodeName</label>
            <CInput
              type="text"
              name="name"
              onChange={(e) =>
                setNewCoupon((old) => ({
                  ...old,
                  couponCodeName: e.target.value,
                }))
              }
              placeholder="Name"
              value={newCoupon?.couponCodeName}
            />
            <br />
            <label>Coupon Discount ( R.s)</label>
            <CInput
              type="number"
              name="discount"
              onChange={(e) =>
                setNewCoupon((old) => ({ ...old, discount: e.target.value }))
              }
              placeholder="discount"
              value={newCoupon?.discount}
            />
            <br />
            <label>Coupon Limit</label>
            <CInput
              type="number"
              name="couponLimit"
              onChange={(e) =>
                setNewCoupon((old) => ({ ...old, couponLimit: e.target.value }))
              }
              placeholder="couponLimit"
              value={newCoupon?.couponLimit}
            />
            <br />
            <label>Coupon ValidPriceRange ( R.s)</label>
            <CInput
              type="number"
              name="name"
              onChange={(e) =>
                setNewCoupon((old) => ({
                  ...old,
                  validPriceRange: e.target.value,
                }))
              }
              placeholder="validPriceRange"
              value={newCoupon?.validPriceRange}
            />
            <br />
            <label>Coupon ExpirationTime ( DD/MM/YYYY)</label>
            <CInput
              type="date"
              name="expirationTime"
              onChange={(e) =>
                setNewCoupon((old) => ({
                  ...old,
                  expirationTime: e.target.value,
                }))
              }
              placeholder="time"
              value={new Date(newCoupon?.expirationTime)
                ?.toJSON()
                ?.substring(0, 10)}
            />
            <br />
            <div className="d-flex justify-content-start">
              <label>Coupon DiscountStatus</label>
              <CInput
                style={{ height: "20px", width: "20px", marginLeft: "40px" }}
                type="checkbox"
                defaultChecked
                name="expirationTime"
                onChange={(e) =>
                  setNewCoupon((old) => ({
                    ...old,
                    discountStatus: e.target.checked,
                  }))
                }
                placeholder="discountStatus"
                value={newCoupon?.discountStatus}
              />
            </div>
          </CModalBody>
          <CModalFooter>
            <CButton onClick={() => setAddModal(false)} color="primary">
              Cancel
            </CButton>
            <CButton
              disabled={
                !newCoupon?.couponCodeName ||
                !newCoupon?.discount ||
                !newCoupon?.expirationTime ||
                !newCoupon?.couponLimit ||
                !newCoupon?.validPriceRange
              }
              onClick={newCoupon?._id ? updateCoupon : addCoupon}
              color="primary"
            >
              Add
            </CButton>
          </CModalFooter>
        </CModal>
      </CCard>
      {event?.type === "error" && (
        <div className="position-fixed fixed-top d-flex justify-content-center">
          <CAlert color="danger">{event.massage}</CAlert>
        </div>
      )}
      {event?.type === "success" && (
        <div className="position-fixed fixed-top d-flex justify-content-center">
          <CAlert color="success">{event.massage}</CAlert>
        </div>
      )}
    </div>
  );
};
export default Coupon;
